import React, { useState } from 'react';
import {
    Form,
    Input,
    Button,
    Select,
} from 'antd';

const { Option } = Select;


const PatientForm = (props) => {
    const [form] = Form.useForm();

    const onFinish = values => {
        console.log(values)
    }

    return (
        <div className="site-layout-content">
            <Form
                form={form}
                labelCol={{
                    span: 4,
                }}
                wrapperCol={{
                    span: 10,
                }}
                layout="horizontal"
                onFinish={onFinish}
            >
                <Form.Item label="Input" name="inp">
                    <Input />
                </Form.Item>
                <Form.Item wrapperCol={{ offset: 4 }}>
                    <Button
                        type="primary"
                        htmlType="submit">
                        Create
                    </Button>
                    <Button
                        style={{marginLeft: "6px"}}
                        type="ghost">
                        Reset
                    </Button>
                </Form.Item>
            </Form>
        </div>
    );
};

export default PatientForm;